<template>
  <div v-loading="loading">
    <div id="main" />
  </div>
</template>
<script>
import echarts from "echarts/lib/echarts";
// 
require("echarts/lib/chart/pie");

export default {
  name: "pieChart",
  data() {
    return {
      loading: true
    };
  },
  methods: {
    setPic() {
      // domecharts
      let myChart = echarts.init(document.getElementById("main"));
      let option = {
        title: {
          text: "",
          subtext: "",
          left: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: ["", "", "", "", ""]
        },
        color: ["#409EFF", "#67C23A", "#E6A23C", "#909399", "#F56C6C"],
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 135, name: "" },
              { value: 310, name: "" },
              { value: 234, name: "" },
              { value: 335, name: "" },
              { value: 1548, name: "" }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      // 
      myChart.setOption(option);
      this.loading = false;
    }
  },
  mounted() {
    this.$nextTick(() => this.setPic());
  }
};
</script>
